body {
  font-size: 1.7rem;
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans';
  color: white;
  background: #141E30;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #243B55, #141E30);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#243B55), to(#141E30));
  background: -webkit-linear-gradient(right, #243B55, #141E30);
  background: -o-linear-gradient(right, #243B55, #141E30);
  background: linear-gradient(to left, #243B55, #141E30); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#header {
  font-family: 'Gugi', sans-serif;
