.title h1 {
  text-align: center;
  margin: 20px;
}

.astros {
  font-family: 'Open Sans';
  height: auto;
  margin-left: auto;
  margin-right:auto;
  padding: 20px;
}

#count {
	text-align: center;
	margin: auto 100px;
}

#count-button {
  -webkit-transition-duration: 0.4s; /* Safari */
  -o-transition-duration: 0.3s;
     transition-duration: 0.3s;
  background-color: #ECECEC;
  color: darkslategray;
  display:block; /* change this from inline-block */
  width:40%; /* setting the width */
  padding: 20px 10px 60px 10px;
  margin:0 auto; /* this will center  it */
  border-width: 0;
  font-size: 2rem;
  border-radius: 10px;
}

#count-button:hover {
  color: #ECECEC;
  background: #2C3E50;
  text-decoration: none;
}

ul {
  text-align: center;
  list-style: none;
  margin-bottom: 80px;
}

li {
  border: 1px solid white;
  border-radius: 8px;
  width: auto;
  display: inline-block;
  margin: 5px 10px 5px 0;
  padding: 4px 10px;
  font-size: 3rem;
  background: rgba(0,0,0,.1);
}

li a {
  color: white;
  text-decoration: none;
  display: block;
  width: 100%;
  height: 100%;
}

.container{
  min-height: 100%;
  position: relative;
}

.footer {
  position: absolute;
  width:100%;
  height:30px;   /* Height of the footer */
  text-align: center;
  font-size: 1vh;
}

.footer p {
  margin-bottom: 0;
}

.footer p a {
  color: whitesmoke;
}

.footer p a:hover {
  color: #1EAEDB;
}



